footer{height:64px;flex:0 0 auto;display:flex;align-items:center;background-color:#2f333a;padding:16px}footer .outer-wrapper{display:flex;align-items:center;justify-content:center;flex:0 0 auto;gap:16px}footer .image-link{display:block;margin:0 auto;margin-bottom:0px}footer .github-block__link{width:24px;height:24px}footer .github-block__logo{width:24px;height:24px;transition:all ease .3s}footer .rss-block__link{height:24px}footer .rss-block__logo{height:24px;transition:all ease .3s;fill:#f00030;filter:invert(1)}footer .developed-year{color:#fff;font-size:14px;line-height:90%;margin-left:auto}footer .developed-year:before{content:"©"}