/* raleway-regular - latin_cyrillic */
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src: url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../../assets/fonts/raleway-v28-latin_cyrillic-regular.svg#Raleway') format('svg'); /* Legacy iOS */
}
